<script>

export default {
    name: 'dt-paper-info',
    props: {
        paper: {
            type: Object,
            required: true,
        },
        person: {
            type: Object,
            default: _ => undefined,
        },
    },
    data() {
        return {
            CLASS: 'Paper',
            isShowInfo: false,
            isCopied: false,
            errCopy: '',
        }
    },
    computed: {
        journalName() {
            if(this.paper.conference && this.paper.conference.name)
                return 'Proc. ' + this.paper.conference.name
            if(!this.paper.journal || !this.paper.journal.name)
                return this.paper.journal_old
            return this.paper.journal.name
        },
        support() {
            if(this.paper.support != 'Megagrant')
                return ``
            return this.isENG
                ? `Is supported by "Megagrant" funding`
                : `Работа выполнена при поддержке "МегаГрантом"`
        },
        authors() {
            if(!this.paper.author)
                return ''
            return this.paper.author.split(' and ').map((a, i, A) => {
                a = a.split(',')
                let name = a[1] ? a[1].trim() : ''
                name = name.length == 1 ? name + '.' : name
                let surname = a[0] && a[0] != 'others' ? a[0].trim() : ''
                let isETC = a[0] == 'others'
                let isInTeam = true
                if(!surname || !this.teamTbl.isLoaded) {
                    isInTeam = false
                }
                else {
                    if(!this.person) {
                        try { this.teamTbl.row(surname.toLowerCase()) }
                        catch(err) { isInTeam = false }
                    }
                    else {
                        isInTeam = this.person.surname == surname
                    }
                }
                let isLast = i == A.length-1
                return {name, surname, isETC, isInTeam, isLast}
            })
        },
        isMega() {
            if(this.paper.support != 'Megagrant')
                return false
            if(this.paper.affiliation != 'Skoltech')
                return false
            if(!this.paper.journal || !this.paper.journal.if)
                return false
            return true
        },
        bib() {
            let bib = []
            if(this.paper.title)
                bib.push(`title={${this.paper.title}}`)
            if(this.paper.author)
                bib.push(`author={${this.paper.author}}`)
            if(this.paper.journal && this.paper.journal.name)
                bib.push(`journal={${this.paper.journal.name}}`)
            if(this.paper.volume)
                bib.push(`volume={${this.paper.volume}}`)
            if(this.paper.number)
                bib.push(`number={${this.paper.number}}`)
            if(this.paper.pages)
                bib.push(`pages={${this.paper.pages}}`)
            if(this.paper.year)
                bib.push(`year={${this.paper.year}}`)
            if(this.paper.journal && this.paper.journal.publisher)
                bib.push(`publisher={${this.paper.journal.publisher}}`)
            if(this.paper.doi)
                bib.push(`doi={${this.paper.doi}}`)
            if(this.paper.url)
                bib.push(`url={${this.paper.url}}`)
            return `@article{${this.paper.tag},\n  ` + bib.join(',\n  ') + `\n}`
        },
        dateConference() {
            if(!this.paper.conference.start || !this.paper.conference.end)
                return ''
            let r1 = this.$moment(this.paper.conference.start.split('T')[0], 'YYYY.MM.DD')
            let r2 = this.$moment(this.paper.conference.end.split('T')[0], 'YYYY.MM.DD')
            if(this.isENG) {
                this.$moment.locale('en');
                r1 = r1.format('MMMM D, YYYY')
                r2 = r2.format('MMMM D, YYYY')
            }
            if(this.isRUS) {
                this.$moment.locale('ru')
                r1 = r1.format('D MMMM, YYYY')
                r2 = r2.format('D MMMM, YYYY')
            }
            return `(${r1} - ${r2})`
        },
        IsEnabledInfo() {
            if(this.journalName == 'ArXiv preprint')
                return false
            if(this.paper.support != 'Megagrant')
                return false
            return true
        },
    },
    methods: {
        onCopy(e) {
            this.isCopied = true
        },
        onCopyError(e) {
            this.isCopied = true
        },
    },
}
</script>

<template lang="pug">

md-list.dt-paper-info(
    md-theme="th3i"
    :md-dense="true"
    :class=`{'md-primary': true,}`)
    md-divider
    md-list-item(
        v-if="paper.volume")
        span.dt-paper-info__field Volume: {{ paper.volume }}
    md-list-item(
        v-if="paper.number")
        span.dt-paper-info__field Number: {{ paper.number }}
    md-list-item(
        v-if="paper.pages")
        span.dt-paper-info__field Number: {{ paper.pages }}
    md-list-item(
        v-if="paper.doi")
        span.dt-paper-info__field Doi: {{ paper.doi }}
    md-list-item(
        v-if="paper.journal && paper.journal.publisher")
        span.dt-paper-info__field
            | Publisher: {{ paper.journal.publisher }}
    md-list-item(
        v-if="paper.journal && paper.journal.issn")
        span.dt-paper-info__field
            | ISSN: {{ paper.journal.issn }}
    md-list-item(
        v-if="paper.journal && paper.journal.country")
        span.dt-paper-info__field
            | Country: {{ paper.journal.country }}
    md-list-item(
        v-if="paper.journal && paper.journal.if")
        span.dt-paper-info__field
            | WoS IF {{ paper.journal.if + (paper.journal.quartile ? ' Q'+paper.journal.quartile : '') }}
    md-list-item(
        v-if="paper.journal && paper.journal.scopus")
        span.dt-paper-info__field
            | Scopus: indexed
    md-list-item(
        v-if="paper.conference && paper.conference.name_full")
        span.dt-paper-info__field
            | Conference: {{ paper.conference.name_full }}
    md-list-item(
        v-if="paper.conference && paper.conference.name_full")
        span.dt-paper-info__field
            | {{ paper.conference.city }}, {{ paper.conference.country }} {{ dateConference }}
    md-list-item(
        v-if="paper.conference && paper.conference.rank")
        span.dt-paper-info__field
            | Rank: Core {{ paper.conference.rank }}
    md-divider
    md-list-item(
        v-if="paper.url"
        :href="paper.url"
        target="_blank")
        span.dt-paper-info__field
            | {{ isENG ? 'Go to publication on the publisher site' : 'Перейти к публикации на сайте издателя' }}
        dkbr-icon(
            name="public")
    md-divider
    md-list-item(
        v-if="paper.journal && paper.journal.site"
        :href="paper.journal.site"
        target="_blank")
        span.dt-paper-info__field
            | {{ isENG ? 'Go to journal site' : 'Перейти на сайт журнала' }}
        dkbr-icon(
            name="public")
    md-list-item(
        v-if="paper.conference && paper.conference.site"
        :href="paper.conference.site"
        target="_blank")
        span.dt-paper-info__field
            | {{ isENG ? 'Go to conference site' : 'Перейти на сайт конференции' }}
        dkbr-icon(
            name="public")
    md-divider
    md-list-item(
        @click=""
        v-clipboard:copy="this.bib"
        v-clipboard:success="onCopy"
        v-clipboard:error="onCopy")
        span.dt-paper-info__field
            | {{ isENG ? 'Copy bibtex to clipboard' : 'Копировать BibTex в буфер' }}
        dkbr-icon(
            name="code")
    md-divider
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-paper-info

</style>



