<script>

import PaperInfo from './PaperInfo'

export default {
    name: 'dt-paper',
    components: {
        'dt-paper-info': PaperInfo,
    },
    props: {
        paper: {
            type: Object,
            required: true,
        },
        person: {
            type: Object,
            default: _ => undefined,
        },
    },
    data() {
        return {
            CLASS: 'Paper',
            isShowInfo: false,
            isCopied: false,
            errCopy: '',
        }
    },
    computed: {
        journalName() {
            if(this.paper.conference && this.paper.conference.name)
                return 'Proc. ' + this.paper.conference.name
            if(!this.paper.journal || !this.paper.journal.name)
                return this.paper.journal_old
            return this.paper.journal.name
        },
        support() {
            if(this.paper.support != 'Megagrant')
                return ``
            return this.isENG
                ? `Is supported by "Megagrant" funding`
                : `Работа выполнена при поддержке "МегаГрантом"`
        },
        authors() {
            if(!this.paper.author)
                return ''
            return this.paper.author.split(' and ').map((a, i, A) => {
                a = a.split(',')
                let name = a[1] ? a[1].trim() : ''
                name = name.length == 1 ? name + '.' : name
                let surname = a[0] && a[0] != 'others' ? a[0].trim() : ''
                let isETC = a[0] == 'others'
                let isInTeam = true
                if(!surname || !this.teamTbl.isLoaded) {
                    isInTeam = false
                }
                else {
                    if(!this.person) {
                        try { this.teamTbl.row(surname.toLowerCase()) }
                        catch(err) { isInTeam = false }
                    }
                    else {
                        isInTeam = this.person.surname == surname
                    }
                }
                let isLast = i == A.length-1
                return {name, surname, isETC, isInTeam, isLast}
            })
        },
        isMega() {
            if(this.paper.support != 'Megagrant')
                return false
            if(this.paper.affiliation != 'Skoltech')
                return false
            if(!this.paper.journal || !this.paper.journal.if)
                return false
            return true
        },
        bib() {
            let bib = []
            if(this.paper.title)
                bib.push(`title={${this.paper.title}}`)
            if(this.paper.author)
                bib.push(`author={${this.paper.author}}`)
            if(this.paper.journal && this.paper.journal.name)
                bib.push(`journal={${this.paper.journal.name}}`)
            if(this.paper.volume)
                bib.push(`volume={${this.paper.volume}}`)
            if(this.paper.number)
                bib.push(`number={${this.paper.number}}`)
            if(this.paper.pages)
                bib.push(`pages={${this.paper.pages}}`)
            if(this.paper.year)
                bib.push(`year={${this.paper.year}}`)
            if(this.paper.journal && this.paper.journal.publisher)
                bib.push(`publisher={${this.paper.journal.publisher}}`)
            if(this.paper.doi)
                bib.push(`doi={${this.paper.doi}}`)
            if(this.paper.url)
                bib.push(`url={${this.paper.url}}`)
            return `@article{${this.paper.tag},\n  ` + bib.join(',\n  ') + `\n}`
        },
        dateConference() {
            if(!this.paper.conference.start || !this.paper.conference.end)
                return ''
            let r1 = this.$moment(this.paper.conference.start.split('T')[0], 'YYYY.MM.DD')
            let r2 = this.$moment(this.paper.conference.end.split('T')[0], 'YYYY.MM.DD')
            if(this.isENG) {
                this.$moment.locale('en');
                r1 = r1.format('MMMM D, YYYY')
                r2 = r2.format('MMMM D, YYYY')
            }
            if(this.isRUS) {
                this.$moment.locale('ru')
                r1 = r1.format('D MMMM, YYYY')
                r2 = r2.format('D MMMM, YYYY')
            }
            return `(${r1} - ${r2})`
        },
        IsEnabledInfo() {
            if(this.journalName == 'ArXiv preprint')
                return false
            if(this.paper.support != 'Megagrant')
                return false
            return true
        },
    },
    methods: {
        onCopy(e) {
            this.isCopied = true
        },
        onCopyError(e) {
            this.isCopied = true
        },
    },
}
</script>

<template lang="pug">

dkbr-frame.dt-paper(
    th="th7"
    :isThInv="true"
    :elevation="!person ? 20 : 0")
    div.dt-paper__header(
        v-if="!person")
        dkbr-icon(
            name="local_library"
            :text="paper.year + (support ? ' | ' + support : '')")
    p.dt-paper__title
        span {{ paper.title }}
        span(
            v-if="journalName") &nbsp;// {{ journalName }}
    div.dt-paper__box
        p.dt-paper__author
            span(
                v-for="(author, i) of authors"
                :key="i"
                :class=`{'dt-paper__author_person': author.isInTeam}`) {{ author.name }} {{ author.isETC ? 'and others' : '' }}{{ author.surname }}{{ author.isLast ? '.' : ', '}}
        div.dt-paper__actions
            dkbr-button.dt-paper__action(
                v-if="IsEnabledInfo"
                th="th3"
                :isRaised="true"
                :isDense="true"
                icon="info"
                @click.native="isShowInfo = !isShowInfo")
            dkbr-button.dt-paper__action(
                th="th3"
                :href="paper.url"
                target="_blank"
                :isRaised="true"
                :isDense="true"
                :desc__="isENG ? 'Go to journal' : 'На страницу журнала'"
                tltpDir__="left"
                icon="public")
    div.dt-paper__info(
        v-if="isShowInfo")
        dt-paper-info(
            :paper="paper")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-paper
    +margin(10px)
    +padding(10px 20px 5px)
    &__box
        +flex(row, nowrap, space-between, center)
    &__header
        +color-font(dark3)
        +font(base-bold-small)
    &__title
        +margin(0)
        +padding(2px 0 5px)
        +color-font(dark3)
        +font(base-bold-big)
    &__author
        flex: 1 0 200px
        +margin(3px 10px 20px)
        +padding(0 20px)
        &_person
            +color-font(light3)
            +font(base-bold)
    &__actions
        flex: 0 0 50px
        +flex(row, nowrap, flex-end)
        +padding-r(10px)
    &__action
        position: relative
        display: block
        +margin(0 10px)
        +padding(0)

</style>



